.feature {
  padding: 40px 0;
}
.feature__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.feature__item {
  margin: 20px 0;
}
.feature__title {
  margin: 0;
  margin-bottom: 20px;
  font-weight: normal;
  font-size: 25px;
  line-height: 1.2;
}
.feature__number {
  display: inline-flex;
  min-width: 32px;
  background: #FFCD00;
  padding: 0 8px;
}

@media (max-width: 1024px) {
  .feature {
    margin-left: 50px;
  }
  .feature__title {
    font-size: 19px;
  }
}
@media (max-width: 767px) {
  .feature {
    margin-left: 0;
  }
  .feature__title {
    font-size: 15px;
  }
  .feature__number {
    min-width: 15px;
  }
}